.App {
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.backgroundContainer {
  position: relative; /* Это необходимо, чтобы создать контекст позиционирования для абсолютно позиционированных элементов внутри него */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.backgroundImage {
  max-width: 100%;
  max-height: 100%;
  z-index: 1;
}

.fogGif {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 36.5%;
  z-index: 2;
  opacity: 0.2;
}

.dateText {
  position: absolute;
  top: 22.5%;
  left: 50%;
  font-family: 'MyCustomFont', sans-serif;  /* 'MyCustomFont' - это имя, которое вы дали шрифту в @font-face */
  transform: translate(-50%, -50%);
  color: white;
  opacity: 0.4;
  font-size: 4.9rem;
  word-wrap: break-word;
  text-align: center;
  user-select: none; /* стандартный синтаксис */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  z-index: 3;
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 1400px) {
  .dateText {
    font-size: 4.4rem;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 1250px) {
  .dateText {
    font-size: 4.0rem;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 1100px) {
  .dateText {
    font-size: 3.5rem;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 950px) {
  .dateText {
    font-size: 2.9rem;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 550px) {
  .dateText {
    font-size: 1.9rem;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 460px) {
  .dateText {
    font-size: 1.3rem;
  }
}

/* Уменьшаем размер шрифта на меньших экранах */
@media (max-width: 275px) {
  .dateText {
    font-size: 0.7rem;
  }
}

@font-face {
  font-family: 'MyCustomFont';  /* Имя, которое вы будете использовать для ссылки на шрифт в CSS */
  src: url('./assets/fonts/1.ttf') format('truetype');  /* Путь к вашему файлу шрифта */
  font-weight: normal;
  font-style: normal;
}
